*, :before, :after {
  box-sizing: border-box;
}

:root {
  --primary-color: #1a3c5a;
  --secondary-color: #0f9d58;
  --tertiary-color: #db4437;
  --quaternary-color: #f4b400;
  --background-color: #fafafa;
  --foreground-color: #fff;
  --text-color-primary: #212121;
  --text-color-secondary: #757575;
  --border-color: #e0e0e0;
  --shadow-light: 0 1px 3px #0000001f, 0 1px 2px #0000003d;
  --shadow-medium: 0 3px 6px #00000029, 0 3px 6px #0000003b;
  --transition-speed: .3s;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.5;
  overflow-x: hidden;
}

body {
  color: var(--text-color-primary);
  background-color: var(--background-color);
  font-family: Roboto, -apple-system, BlinkMacSystemFont, Segoe UI, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

.main-layout {
  background-color: var(--background-color);
  flex-direction: column;
  width: 100%;
  max-width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.app-header {
  text-align: center;
  color: #fff;
  box-shadow: var(--shadow-medium);
  z-index: 10;
  background-color: #1a3c5a;
  margin-bottom: 0;
  padding: 30px 0;
  position: relative;
}

.header-content {
  max-width: 800px;
  margin: 0 auto;
}

.app-header h1 {
  color: #fff;
  letter-spacing: .5px;
  margin: 0;
  font-size: 2.4rem;
  font-weight: 300;
}

.header-prefix {
  font-weight: 300;
}

.header-highlight {
  font-weight: 500;
}

.app-header p {
  color: #ffffffd9;
  margin: 10px 0 0;
  font-size: 1.1rem;
  font-weight: 300;
}

.app-main {
  flex-direction: column;
  flex: 1;
  width: 100%;
  padding: 0;
  display: flex;
  position: relative;
  overflow-y: auto;
}

.app-footer {
  text-align: center;
  color: var(--text-color-secondary);
  border-top: 1px solid var(--border-color);
  background-color: var(--foreground-color);
  padding: 10px 0;
  font-size: .85rem;
}

.app-footer a {
  color: var(--primary-color);
  text-decoration: none;
}

.app-footer a:hover {
  text-decoration: underline;
}

.app-section {
  background-color: var(--foreground-color);
  box-shadow: var(--shadow-light);
  transition: box-shadow var(--transition-speed);
  border-radius: 4px;
  margin-bottom: 8px;
  padding: 15px;
}

.app-section:hover {
  box-shadow: var(--shadow-medium);
}

.section-title {
  color: var(--text-color-primary);
  border-bottom: 1px solid var(--border-color);
  margin-top: 0;
  margin-bottom: 15px;
  padding-bottom: 8px;
  font-size: 1.2rem;
  font-weight: 500;
}

.section-content {
  padding: 5px 0;
}

.two-column-layout {
  flex: 1;
  grid-template-columns: 350px 1fr;
  gap: 10px;
  width: 100%;
  padding: 10px;
  display: grid;
}

@media (width <= 768px) {
  .two-column-layout {
    grid-template-columns: 1fr;
  }
}

.simulation-controls {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.prominent-button-container {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin: 5px 0 15px;
  display: flex;
}

.prominent-run-button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .5px;
  border: none;
  border-radius: 30px;
  min-width: 160px;
  padding: 10px 24px;
  font-size: 1.1rem;
  font-weight: 500;
  transition: all .2s;
  box-shadow: 0 2px 5px #0003;
}

.prominent-run-button:hover {
  background-color: #25517a;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #0003;
}

.prominent-run-button:active {
  background-color: #142b41;
  transform: translateY(1px);
  box-shadow: 0 1px 3px #0003;
}

.control-group {
  flex-direction: column;
  margin-bottom: 12px;
  display: flex;
}

.control-group label {
  color: var(--text-color-secondary);
  margin-bottom: 5px;
  font-size: 1rem;
  font-weight: 500;
}

.control-group input[type="number"], .control-group input[type="text"], .control-group select, .control-group option {
  border: 1px solid var(--border-color);
  transition: border-color var(--transition-speed);
  border-radius: 4px;
  padding: 8px 10px;
  font-size: 16px;
}

.control-group input[type="number"]:focus, .control-group input[type="text"]:focus, .control-group select:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px #4285f433;
}

.radio-group {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.radio-group label {
  cursor: pointer;
  align-items: center;
  font-weight: normal;
  display: flex;
}

.radio-group input[type="radio"] {
  accent-color: var(--primary-color);
  margin-right: 8px;
}

.button-group {
  gap: 10px;
  margin-top: 15px;
  display: flex;
}

button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  transition: background-color var(--transition-speed), transform var(--transition-speed);
  box-shadow: var(--shadow-light);
  text-transform: uppercase;
  letter-spacing: .5px;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 16px;
  font-weight: 500;
}

button:hover {
  box-shadow: var(--shadow-medium);
  background-color: #25517a;
  transform: translateY(-1px);
}

button:active {
  background-color: #142b41;
  transform: translateY(1px);
}

.reset-button {
  background-color: #757575;
  border-radius: 30px;
  padding: 8px 20px;
  font-size: 1rem;
}

.reset-button:hover {
  background-color: #616161;
}

.rule-matrix-editor {
  margin-top: 20px;
}

.matrix-container {
  margin: 10px 0;
  overflow-x: auto;
}

.matrix-vertical-container {
  flex-direction: column;
  align-items: center;
  gap: 15px;
  max-width: 100%;
  margin: 10px 0;
  display: flex;
}

.matrix-section {
  flex-direction: row;
  align-items: center;
  gap: 15px;
  width: 100%;
  margin-bottom: 5px;
  display: flex;
}

.matrix-section .matrix-label {
  color: var(--text-color-primary);
  text-align: right;
  width: 80px;
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: bold;
}

.number-picker {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 0 2px;
  display: flex;
  overflow: hidden;
}

.number-picker-btn {
  color: #333;
  cursor: pointer;
  width: 24px;
  height: 24px;
  box-shadow: none;
  min-width: unset;
  text-transform: none;
  letter-spacing: normal;
  background-color: #f5f5f5;
  border: none;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: normal;
  transition: background-color .2s;
  display: flex;
}

.number-picker-btn:hover {
  box-shadow: none;
  background-color: #ebebeb;
  transform: none;
}

.number-picker-btn:active {
  background-color: #e0e0e0;
  transform: none;
}

.number-picker-value {
  text-align: center;
  flex: 1;
  min-width: 12px;
  padding: 0 4px;
  font-size: 14px;
  font-weight: bold;
}

.matrix-labels {
  color: var(--text-color-secondary);
  justify-content: space-around;
  width: 100%;
  margin-bottom: 5px;
  font-size: .85rem;
  font-weight: 500;
  display: flex;
}

.matrix-label-col {
  flex-direction: column;
  align-items: center;
  width: 33.33%;
  display: flex;
}

.matrix-label {
  text-align: center;
  margin-bottom: 2px;
}

.matrix-label-cells {
  justify-content: center;
  gap: 2px;
  display: flex;
}

.matrix-label-cell {
  visibility: hidden;
  width: 32px;
}

.matrix-table {
  border-collapse: separate;
  border-spacing: 2px;
  table-layout: fixed;
  width: 100%;
}

.matrix-table td {
  vertical-align: middle;
  width: 40px;
  height: 40px;
  padding: 2px;
}

.matrix-table input {
  text-align: center;
  border: 1px solid var(--border-color);
  width: 40px;
  height: 40px;
  transition: background-color var(--transition-speed), border-color var(--transition-speed);
  color: var(--text-color-primary);
  border-radius: 4px;
  padding: 0;
  font-size: 1.1rem;
}

.matrix-table input:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px #4285f433;
}

.left-matrix {
  background-color: #db44371a;
}

.center-matrix {
  background-color: #4285f41a;
}

.right-matrix {
  background-color: #0f9d581a;
}

.reset-matrix-button {
  background-color: #757575;
  margin-top: 10px;
}

.visualization-container {
  background-color: var(--foreground-color);
  width: 100%;
  box-shadow: var(--shadow-light);
  min-height: 200px;
  transition: box-shadow var(--transition-speed);
  border-radius: 4px;
  margin: 10px 0;
  padding: 0;
  overflow-x: auto;
}

.mathematical-analysis {
  margin-top: 15px;
}

.quantum-pane {
  background-color: #fffc;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 15px;
  padding: 15px;
}

.quantum-content {
  flex-direction: column;
  display: flex;
}

.quantum-info {
  margin-bottom: 15px;
}

.quantum-info p {
  margin: 5px 0;
  font-size: 14px;
}

.realtime-notice {
  color: #2196f3;
  background-color: #2196f31a;
  border-radius: 3px;
  padding: 5px;
  font-weight: bold;
  animation: 2s infinite pulse;
  display: inline-block;
  margin-top: 10px !important;
}

@keyframes pulse {
  0% {
    opacity: .7;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: .7;
  }
}

.info-label {
  color: #666;
  margin-left: 5px;
  font-size: 12px;
}

.quantum-content ul {
  margin-left: 20px;
  padding-left: 0;
}

.quantum-content li {
  margin-bottom: 5px;
  padding-left: 25px;
  list-style-type: none;
  position: relative;
}

.quantum-content li:before {
  content: "✓";
  color: var(--secondary-color);
  font-weight: bold;
  position: absolute;
  left: 0;
}

.message-box {
  opacity: .7;
  background-color: #f8f9fa;
  border-left: 4px solid #ccc;
  border-radius: 8px;
  padding: 15px;
}

.property-display {
  background-color: #f8f9fa;
  border-radius: 8px;
  margin-bottom: 15px;
  padding: 12px;
  box-shadow: 0 1px 3px #0000001a;
}

.property-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  display: flex;
}

.property-name {
  color: #333;
  font-size: 1.1rem;
  font-weight: 600;
}

.property-status {
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  font-size: 1.2rem;
  font-weight: bold;
  display: flex;
}

.property-status.valid {
  color: #fff;
  background-color: #0f9d58;
}

.property-status.invalid {
  color: #fff;
  background-color: #db4437;
}

.property-details {
  white-space: pre-wrap;
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  padding: 8px;
  font-family: Courier New, monospace;
  font-size: .9rem;
}

.visualization-container:hover {
  box-shadow: var(--shadow-medium);
}

#spacetime-diagram {
  height: calc(100vh - 500px);
  min-height: 400px;
  max-height: 85vh;
  display: flex;
}

.matrix-table input[type="number"] {
  text-align: center;
  color: var(--text-color-primary);
  -moz-appearance: textfield;
  font-size: 1.1rem;
  font-weight: bold;
}

.matrix-table input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.matrix-table input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.tooltip {
  color: #fff;
  pointer-events: none;
  z-index: 1000;
  box-shadow: var(--shadow-medium);
  background-color: #212121e6;
  border-radius: 4px;
  max-width: 250px;
  padding: 8px 12px;
  font-size: .85rem;
  position: absolute;
}

input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.disabled-container {
  opacity: .6;
  pointer-events: none;
}

.prominent-stop-button {
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .5px;
  background-color: #db4437;
  border: none;
  border-radius: 30px;
  min-width: 160px;
  padding: 10px 24px;
  font-size: 1.1rem;
  font-weight: 500;
  transition: all .2s;
  box-shadow: 0 2px 5px #0003;
}

.prominent-stop-button:hover {
  background-color: #c13b30;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #0003;
}

.prominent-stop-button:active {
  background-color: #a0352c;
  transform: translateY(1px);
  box-shadow: 0 1px 3px #0003;
}

input:disabled, select:disabled, button:disabled {
  cursor: not-allowed;
  opacity: .6;
  background-color: #f5f5f5;
}

.logical-qubits-info {
  background-color: #0f9d581a;
  border-left: 4px solid #0f9d58;
  border-radius: 8px;
  margin-top: 20px;
  padding: 15px;
}

.logical-qubits-info h4 {
  color: #0f9d58;
  margin-top: 0;
}

.logical-qubits-info strong {
  color: #0f9d58;
  font-size: 1.2rem;
}

.logical-qubits-info em {
  color: var(--text-color-secondary);
  font-style: italic;
}

.trajectory-chart {
  background-color: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  margin-top: 15px;
  padding: 15px;
}

.trajectory-chart h4 {
  color: var(--text-color-primary);
  text-transform: uppercase;
  letter-spacing: .5px;
  margin: 0 0 10px;
  font-size: .9rem;
  font-weight: 500;
}

.trajectory-chart svg {
  background: linear-gradient(135deg, #f8f9fa 0%, #fff 100%);
  border: 1px solid #dee2e6;
  border-radius: 6px;
  margin: 0 auto;
  display: block;
  box-shadow: 0 2px 4px #0000001a;
}

.trajectory-chart .chart-line {
  stroke: #2563eb;
  stroke-width: 2.5px;
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  filter: drop-shadow(0 1px 2px #2563eb33);
}

.trajectory-chart .chart-point {
  fill: #2563eb;
  stroke: #fff;
  stroke-width: 1.5px;
  filter: drop-shadow(0 1px 2px #0000001a);
}

.trajectory-chart .chart-point:hover {
  fill: #1d4ed8;
  r: 4;
}

.trajectory-chart .chart-label {
  fill: #6b7280;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 500;
}

.trajectory-chart .chart-title {
  fill: #374151;
  text-anchor: middle;
  font-family: Roboto, sans-serif;
  font-size: 11px;
  font-weight: 500;
}
